import {
  IRequestBodyAI,
  IRequestParamsAI,
} from '../app/components/ChatAI/helper';
import {getConfig} from './httpClient';

export const createAIServices = (fetchRequest: any) => ({
  async sendMessage(body: IRequestBodyAI) {
    try {
      const endpoint = '/services/eintelligent/api/eRequest/messages';
      const data = await fetchRequest({
        url: getConfig().BASE_URL + endpoint,
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(body),
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('sendMessage: ', error);
      return Promise.reject(error);
    }
  },
  async getMessage(params: IRequestParamsAI) {
    try {
      const endpoint = '/services/eintelligent/api/eRequest/messages';
      const data = await fetchRequest({
        url: getConfig().BASE_URL + endpoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
        params,
      });
      const totalData = data?.headers.get('x-total-count') ?? 0;
      return Promise.resolve({
        data: data?.data,
        totalData: totalData,
      });
    } catch (error) {
      console.log('getMessage: ', error);
      return Promise.reject(error);
    }
  },
});
