import {FlatList, View} from 'react-native';
import TextCM from '../../../../../../components/Text';
import {useEffect, useState} from 'react';
import {useDashboardServices} from '../../../../../../../hooks/apiHooks/useAPI';
import {ICountService} from '../../helper';
import styles from './styles';
import {useTranslation} from 'react-i18next';

const ServiceRate = () => {
  const {t} = useTranslation();
  const [dataService, setDataService] = useState<ICountService[]>();
  const DashboardService = useDashboardServices();

  useEffect(() => {
    getCountByService();
  }, []);
  const getCountByService = async () => {
    const dataResp = await DashboardService.countByServiceRate();
    if (dataResp) {
      setDataService(dataResp?.slice(0, 3));
    }
  };
  return (
    <View style={{flex: 1}}>
      <View style={styles.ctnHeader}>
        <TextCM style={styles.txtHeader}>{t('DB.rate_service')}</TextCM>
      </View>

      <FlatList
        data={dataService}
        showsVerticalScrollIndicator={false}
        contentContainerStyle={{rowGap: 16}}
        renderItem={({item, index}) => (
          <View
            style={{
              ...styles.ctnItem,
              ...styles[`top_${index + 1}`],
            }}>
            <TextCM style={styles.txt_1}>Top {index + 1}</TextCM>
            <TextCM style={styles.txt_2}>{item.serviceName}</TextCM>
            <TextCM style={styles.txt_3}>
              {item.totalTickets} {t('DB.ticket-rated-5')}
            </TextCM>
          </View>
        )}
        ListEmptyComponent={() => <TextCM>{t('DB.no-data')}</TextCM>}
      />
    </View>
  );
};

export default ServiceRate;
