import {TouchableOpacity, View} from 'react-native';
import {useCallback, useEffect, useState} from 'react';
import ModalFullScreen from '../../../../../../components/ModalFullScreen';
import HeaderCM from '../../../../../../components/Header/HeaderCM';
import {Divider, Icon, IndexPath} from '@ui-kitten/components';
import DropdownListCM from '../../../../../../components/DropDownList';
import {fullMonthList, precious, timeTypeList} from './helper';
import InputDate from '../../../../../review-econtract-flow/components/InputDate';
import ButtonCM from '../../../../../../components/Button';
import {useTranslation} from 'react-i18next';

type Props = {
  setDataFilter: any;
};

export interface IFilter {
  year?: string;
  month?: string;
  endDate?: string;
  startDate?: string;
}

const Filter = ({setDataFilter}: Props) => {
  const {t} = useTranslation();
  const [showModal, setShowModal] = useState(false);
  const [showPeriod, setShowPeriod] = useState(false);
  const [listYear, setListYear] = useState<any[]>([]);
  const [timeType, setTimeType] = useState<IndexPath>(new IndexPath(0));
  const [timeYear, setTimeYear] = useState<IndexPath>(new IndexPath(0));
  const [timeQuarter, setTimeQuarter] = useState<IndexPath>();
  const [timeMonth, setTimeMonth] = useState<IndexPath>();
  const [dataFilterShow, setDataFilterShow] = useState<IFilter>({
    year: new Date().getFullYear().toString(),
    month: '',
    endDate: '',
    startDate: '',
  });

  useEffect(() => {
    setDataFilter(dataFilterShow);
    setListYear(
      Array.from({length: 30}, (_, i) => ({
        label: (new Date().getFullYear() - i).toString(),
        value: (new Date().getFullYear() - i).toString(),
      })),
    );
  }, []);

  const handleChangeTimeType = (index: IndexPath) => {
    setTimeType(index);
    if (index.row === 0) {
      setShowPeriod(false);
      setTimeYear(new IndexPath(0));
      setTimeQuarter(undefined);
      setTimeMonth(undefined);
      setDataFilterShow({
        year: new Date().getFullYear().toString(),
        month: '',
        endDate: '',
        startDate: '',
      });
    } else {
      setShowPeriod(true);
      setDataFilterShow({
        year: '',
        month: '',
        endDate: '',
        startDate: '',
      });
    }
  };

  const handleChangeDataYear = (index: IndexPath) => {
    setTimeYear(index);
    setDataFilterShow(prev => ({...prev, year: listYear?.[index?.row]?.value}));
  };

  const handleSelectDate = (data: any, type: string) => {
    setDataFilterShow(prev => ({
      ...prev,
      [type]: data,
    }));
  };
  const getListMonth = useCallback(() => {
    if (!timeQuarter) return fullMonthList;
    return precious?.[timeQuarter?.row]?.child;
  }, [timeQuarter, timeYear]);

  const getQuarterDate = (quarter: string) => {
    let year = Number(dataFilterShow?.year) || new Date().getFullYear();
    let fromMonth = 0,
      toMonth = 0;
    if (quarter == 'quarter1') {
      fromMonth = 0;
      toMonth = 2;
    } else if (quarter == 'quarter2') {
      fromMonth = 3;
      toMonth = 5;
    } else if (quarter == 'quarter3') {
      fromMonth = 6;
      toMonth = 8;
    } else if (quarter == 'quarter4') {
      fromMonth = 9;
      toMonth = 11;
    }
    const fromDate = new Date(year, fromMonth, 1, 0, 0, 0);
    const toDate = new Date(
      year,
      toMonth,
      quarter === 'quarter2' || quarter === 'quarter3' ? 30 : 31,
      23,
      59,
      59,
    );
    return {fromDate, toDate};
  };

  const getDayInMonth = (month: any, year: any) => {
    const daysInMonthsCommonYear = [
      31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31,
    ];
    const daysInMonthsLeapYear = [
      31, 29, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31,
    ];

    const isLeapYear = (year % 4 === 0 && year % 100 !== 0) || year % 400 === 0;

    return isLeapYear
      ? daysInMonthsLeapYear[month - 1]
      : daysInMonthsCommonYear[month - 1];
  };

  const getMonthDate = (month: any) => {
    let year = Number(dataFilterShow?.year) || new Date().getFullYear();
    let fromMonth, toMonth;

    fromMonth = month - 1;
    toMonth = month - 1;

    const fromDate = new Date(year, fromMonth, 1, 0, 0, 0);
    const toDate = new Date(
      year,
      toMonth,
      Number(getDayInMonth(month + 1, year)),
      23,
      59,
      59,
    );
    return {fromDate, toDate};
  };

  const handleSubmit = () => {
    try {
      if (timeType?.row === 0) {
        if (!timeQuarter) {
          setDataFilter(dataFilterShow);
          return;
        }
        if (!timeMonth) {
          const {fromDate, toDate} = getQuarterDate(
            precious[timeQuarter?.row]?.value,
          );
          setDataFilterShow(prev => ({
            ...prev,
            startDate: fromDate.toISOString(),
            endDate: toDate.toISOString(),
          }));
          setDataFilter({
            ...dataFilterShow,
            startDate: fromDate.toISOString(),
            endDate: toDate.toISOString(),
          });
          return;
        }
        const listMonth = getListMonth();
        const {fromDate, toDate} = getMonthDate(
          listMonth[timeMonth?.row]?.value,
        );
        setDataFilter({
          ...dataFilterShow,
          startDate: fromDate.toISOString(),
          endDate: toDate.toISOString(),
        });
      } else {
        setDataFilter({
          ...dataFilterShow,
        });
      }
    } catch (error) {
    } finally {
      setShowModal(false);
    }
  };

  return (
    <>
      <TouchableOpacity
        onPress={() => setShowModal(true)}
        style={{
          borderWidth: 1,
          borderColor: '#8f4eff',
          paddingHorizontal: 6,
          paddingVertical: 6,
          borderRadius: 5,
        }}>
        <Icon name="funnel-outline" width={16} height={16} fill={'#8f4eff'} />
      </TouchableOpacity>

      <ModalFullScreen
        isVisible={showModal}
        children={
          <>
            <HeaderCM
              style={{
                backgroundColor: '#fff',
              }}
              contentStyle={{
                backgroundColor: '#fff',
              }}
              titleStyle={{
                color: '#000810',
              }}
              fillIconBackLeft="#000810"
              onPressIconLeft={() => setShowModal(false)}
              title={t('CM.filter')}
            />
            <Divider />

            <View
              style={{
                padding: 10,
                flex: 1,
              }}>
              <DropdownListCM
                size="medium"
                onSelect={handleChangeTimeType}
                valueSelect={timeType}
                label={t('DB.filter.type-time')}
                listData={timeTypeList}
                isi18n={true}
              />
              {!showPeriod ? (
                <>
                  <DropdownListCM
                    onSelect={handleChangeDataYear}
                    valueSelect={timeYear}
                    label={t('DB.filter.year')}
                    size="medium"
                    listData={listYear}
                    isi18n={true}
                  />

                  <DropdownListCM
                    onSelect={(item: IndexPath) => {
                      setTimeQuarter(item);
                      setTimeMonth(undefined);
                    }}
                    size="medium"
                    valueSelect={timeQuarter}
                    label={t('DB.filter.precious')}
                    listData={precious}
                    isi18n={true}
                    placeholder={t('DB.filter.precious-placeholder')}
                  />
                  <DropdownListCM
                    onSelect={setTimeMonth}
                    valueSelect={timeMonth}
                    label={t('DB.filter.month')}
                    size="medium"
                    listData={getListMonth()}
                    isi18n={true}
                    placeholder={t('DB.filter.month-placeholder')}
                  />
                </>
              ) : (
                <View
                  style={{
                    flexDirection: 'row',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    marginTop: 10,
                  }}>
                  <InputDate
                    value={dataFilterShow.startDate}
                    onChangeDate={value => handleSelectDate(value, 'startDate')}
                  />
                  <Icon name="arrow-forward-outline" width={20} height={20} />
                  <InputDate
                    value={dataFilterShow.endDate}
                    onChangeDate={value => handleSelectDate(value, 'endDate')}
                  />
                </View>
              )}
            </View>

            <ButtonCM
              style={{marginHorizontal: 16}}
              label={t('CM.confirm')}
              onPress={handleSubmit}
            />
          </>
        }
      />
    </>
  );
};

export default Filter;
