import {useWindowDimensions, View} from 'react-native';
import TextCM from '../../../../../../components/Text';
import Filter, {IFilter} from '../filter';
import {useEffect, useState} from 'react';
import {useDashboardServices} from '../../../../../../../hooks/apiHooks/useAPI';
import {colorList, ICountService} from '../../helper';
import styles from './styles';
import {BarChart} from 'react-native-gifted-charts';
import {Color, Font} from '../../../../../../../constants';
import {useTranslation} from 'react-i18next';
const ServicesDelay = () => {
  const {t} = useTranslation();
  const [dataFilter, setDataFilter] = useState<IFilter>();
  const [dataService, setDataService] = useState<any[]>([]);
  const {width} = useWindowDimensions();
  const DashboardService = useDashboardServices();

  useEffect(() => {
    if (dataFilter) {
      getCountByService(dataFilter);
    }
  }, [dataFilter]);
  const getCountByService = async (dataFilter?: IFilter) => {
    const dataResp = await DashboardService.ticketDelay(dataFilter);
    if (dataResp) {
      setDataService(
        dataResp?.map((item: ICountService) => ({
          value: item.totalTickets,
          label: item.serviceName,
          frontColor: colorList[Math.floor(Math.random() * colorList.length)],
        })),
      );
    }
  };
  return (
    <View style={{flex: 1}}>
      <View style={styles.ctnHeader}>
        <TextCM style={styles.txtHeader}>{t('DB.ticket-late')}</TextCM>
        <Filter setDataFilter={setDataFilter} />
      </View>

      <View
        style={{
          alignItems: 'center',
          justifyContent: 'center',
          flex: 1,
        }}>
        <BarChart
          barWidth={20}
          width={width - 100}
          barBorderRadius={4}
          frontColor="lightgray"
          data={dataService}
          yAxisThickness={0.5}
          xAxisThickness={0.5}
          xAxisLabelTextStyle={{
            fontSize: 8,
            fontFamily: Font.InterSemiBold600,
            color: Color.Text,
          }}
          yAxisTextStyle={{
            fontSize: 12,
            fontFamily: Font.InterSemiBold600,
            color: Color.Text,
          }}
          xAxisTextNumberOfLines={2}
        />
      </View>
    </View>
  );
};

export default ServicesDelay;
