//import liraries
import React from 'react';
import {FlatList, Pressable, View} from 'react-native';
import styles from './styles';
import TextCM from '../../components/Text';
import {useNavigation} from '@react-navigation/native';
import HeaderCM from '../../components/Header/HeaderCM';
import {Divider, Icon} from '@ui-kitten/components';
import {useTranslation} from 'react-i18next';
import Dashboard from './component/Dashboard';
import {ListServices} from './helper';
import {useZutandStore} from '../../../zutand/store';

const Home = () => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const {listMenuViewERequest} = useZutandStore();

  const getListService = () => {
    return ListServices?.filter(i => {
      const data = listMenuViewERequest?.find(m => m.nameId === i.permission);
      if (data) return i;
    });
  };
  return (
    <>
      <HeaderCM title="eRequest" />
      <View style={{flex: 1, backgroundColor: '#fff'}}>
        <View style={styles.ctnMain}>
          <TextCM style={styles.textTitle}>{t('HM.title')}</TextCM>
          <FlatList
            data={getListService()}
            horizontal
            style={{
              marginTop: 10,
            }}
            contentContainerStyle={{
              gap: 16,
            }}
            showsHorizontalScrollIndicator={false}
            renderItem={({item}) => (
              <Pressable
                onPress={() => navigation.navigate(item.navigationName)}
                style={styles.ctnPress}>
                <View style={styles.ctnCheck}>
                  <Icon fill={'#fff'} name={item.icon} width={20} height={20} />
                </View>
                <TextCM style={styles.txtPress}>{t(item.name)}</TextCM>
              </Pressable>
            )}
            ListEmptyComponent={() => <TextCM>{t('DB.no-data')}</TextCM>}
          />
        </View>
        <Divider style={{height: 6}} />
        {listMenuViewERequest?.find(
          m => m.nameId === 'sidebar.admin.dashboard',
        ) && <Dashboard />}
      </View>
    </>
  );
};

//make this component available to the app
export default Home;
