import {useWindowDimensions} from 'react-native';
import Services from './component/services';
import {TabView, SceneMap, TabBar} from 'react-native-tab-view';
import {useState} from 'react';
import {Color, Font} from '../../../../../constants';
import ServicesRecent from './component/recent-ticket';
import TicketRate from './component/ticket-rate';
import ServiceRate from './component/service-rate';
import ServicesDelay from './component/ticket-delay-by-service';
import ServicesStatus from './component/tickets-status';
import {useTranslation} from 'react-i18next';

const Dashboard = () => {
  const {t} = useTranslation();
  const layout = useWindowDimensions();
  const [index, setIndex] = useState(0);
  const renderScene = SceneMap({
    service: Services,
    status: ServicesStatus,
    delay: ServicesDelay,
    recent: ServicesRecent,
    ticketRate: TicketRate,
    serviceRate: ServiceRate,
  });

  const routes = [
    {key: 'service', title: t('DB.ticket-service')},
    {key: 'status', title: t('DB.ticket-status')},
    {key: 'delay', title: t('DB.ticket-late')},
    {key: 'recent', title: t('DB.ticket-nearest')},
    {key: 'ticketRate', title: t('DB.rate_ticket')},
    {key: 'serviceRate', title: t('DB.rate_service')},
  ];
  const renderTabBar = (props: any) => (
    <TabBar
      {...props}
      indicatorStyle={{backgroundColor: Color.BgPrimary}}
      style={{backgroundColor: '#fff'}}
      scrollEnabled
      labelStyle={{
        color: Color.BgPrimary,
        fontFamily: Font.InterSemiBold600,
        fontSize: 10,
        textAlign: 'center',
      }}
    />
  );
  return (
    <>
      <TabView
        lazy
        style={{backgroundColor: '#fff'}}
        sceneContainerStyle={{paddingHorizontal: 16, paddingVertical: 10}}
        renderTabBar={renderTabBar}
        navigationState={{index, routes}}
        renderScene={renderScene}
        onIndexChange={setIndex}
        initialLayout={{width: layout.width}}
        swipeEnabled={false}
      />
    </>
  );
};

export default Dashboard;
