import {FlatList, ScrollView, useWindowDimensions, View} from 'react-native';
import TextCM from '../../../../../../components/Text';
import Filter, {IFilter} from '../filter';
import {useEffect, useState} from 'react';
import {useDashboardServices} from '../../../../../../../hooks/apiHooks/useAPI';
import {CHART_COUNT_BY_STATUS_DATA} from '../../helper';
import styles from './styles';
import {PieChart} from 'react-native-gifted-charts';
import {useTranslation} from 'react-i18next';
import {Font} from '../../../../../../../constants';

const ServicesStatus = () => {
  const {t} = useTranslation();
  const [dataFilter, setDataFilter] = useState<IFilter>();
  const [dataService, setDataService] = useState<
    {
      value: string;
      text: string;
      color: string;
      label: string;
    }[]
  >([]);
  const DashboardService = useDashboardServices();
  const layout = useWindowDimensions();

  useEffect(() => {
    if (dataFilter) {
      getCountByService(dataFilter);
    }
  }, [dataFilter]);
  const getCountByService = async (dataFilter?: IFilter) => {
    const dataResp = await DashboardService.getCountByStatus(dataFilter);
    if (dataResp) {
      setDataService(
        dataResp?.map((item: any) => ({
          value: item.totalTickets,
          text: item.totalTickets,
          color: CHART_COUNT_BY_STATUS_DATA?.find(
            d => d.value === item.ticketStatus,
          )?.backgroundColor,
          label: CHART_COUNT_BY_STATUS_DATA?.find(
            d => d.value === item.ticketStatus,
          )?.label,
        })),
      );
    }
  };

  return (
    <View style={{flex: 1}}>
      <View style={styles.ctnHeader}>
        <TextCM style={styles.txtHeader}>{t('DB.ticket-status')}</TextCM>
        <Filter setDataFilter={setDataFilter} />
      </View>

      {dataService?.length === 0 && <TextCM>{t('DB.no-data')}</TextCM>}
      <ScrollView contentContainerStyle={styles.ctnBody}>
        <PieChart
          donut
          radius={layout.width / 4}
          textSize={13}
          data={dataService as any}
        />
        <FlatList
          scrollEnabled={false}
          data={dataService}
          renderItem={({item}) => (
            <View style={styles.ctnSubBody}>
              <View
                style={{
                  ...styles.ctnImage,
                  backgroundColor: item.color,
                }}></View>
              <TextCM style={styles.txtDescription}>
                {t(`TK.${item.label}`)}{' '}
                <TextCM
                  style={{
                    fontFamily: Font.InterSemiBold600,
                    fontSize: 12,
                  }}>
                  ({item.value})
                </TextCM>
              </TextCM>
            </View>
          )}
        />
      </ScrollView>
    </View>
  );
};

export default ServicesStatus;
