import {Image, TouchableOpacity, View} from 'react-native';
import {IconTrash} from '../../../../../assets/images';
import styles from './styles';
import TextCM from '../../../../components/Text';
import {} from '../../../../../constants/Size';
import {Color, Font, String} from '../../../../../constants';
import {Status} from '../../../../../model/enum/common.enum';
import {ITicket} from '../../../../../model/interface/service.interface';
import {FC, useState} from 'react';
import {DateUtils} from '../../../../../utils';
import {IAccount} from '../../../../../model/interface/account.interface';
import {useTranslation} from 'react-i18next';
import ModalConfirmCM from '../../../../components/ModalConfirm';

type Props = {
  dataCard: ITicket;
  onSelect: (listSelect: ITicket[]) => void;
  select: ITicket[];
  account: IAccount;
};

const CardTicketRelative: FC<Props> = ({
  dataCard,
  onSelect,
  select,
  account,
}) => {
  const {t} = useTranslation();
  const [modalConfirm, setIsModalConfirm] = useState(false);
  const handleClearData = () => {
    const filterData = select.filter(item => item.id !== dataCard.id);
    onSelect(filterData);
  };

  const renderContent = (color: string, bgColor: string, status: Status) => {
    return (
      <View
        style={{
          flexDirection: 'row',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}>
        <View
          style={{
            backgroundColor: bgColor,
            paddingHorizontal: 10,
            paddingVertical: 6,
            borderRadius: 8,
            alignItems: 'center',
            justifyContent: 'center',
            marginTop: 6,
          }}>
          <TextCM style={{color: color}}>{t(`TK.${status}`)}</TextCM>
        </View>
        <View style={{flex: 1}}></View>
      </View>
    );
  };

  const renderStatusTicket = () => {
    switch (dataCard?.ticketStatus) {
      case Status.completed:
        return renderContent(
          Color.completed,
          Color.completed01,
          Status.completed,
        );
      case Status.draft:
        return renderContent(Color.draft, Color.draft01, Status.draft);
      case Status.voided:
        return renderContent(Color.voided, Color.voided01, Status.voided);
      case Status.rejected:
        return renderContent(Color.rejected, Color.rejected01, Status.rejected);
      case Status.processing:
        return renderContent(
          Color.processing,
          Color.processing01,
          Status.processing,
        );
      case Status.overdue:
        return renderContent(Color.rejected, Color.rejected01, Status.overdue);
    }
  };

  return (
    <View style={styles.ctnMain}>
      <View style={{rowGap: 6}}>
        <TextCM
          style={{
            fontFamily: Font.InterSemiBold600,
            fontSize: 16,
          }}>
          {dataCard?.title}
        </TextCM>
        {/* <TextCM style={styles.subText}>Nhn s </TextCM> */}
        <View style={styles.ctnInfo}>
          <TextCM>
            {DateUtils.convertDateToDDMMYYYY_H_M(dataCard?.createdDate)}
          </TextCM>

          {account?.avatar ? (
            <View style={styles.ctnImage}>
              <Image
                width={24}
                height={24}
                style={{borderRadius: 12}}
                source={{uri: String.PREFIX_IMG_BASE64 + account?.avatar}}
              />
              <TextCM numberOfLines={1} style={{width: 150}}>
                {dataCard?.createdBy}
              </TextCM>
            </View>
          ) : (
            <View style={styles.ctnImage}>
              <View
                style={{
                  width: 24,
                  height: 24,
                  backgroundColor: 'red',
                  borderRadius: 12,
                  justifyContent: 'center',
                  alignItems: 'center',
                }}></View>
              <TextCM numberOfLines={1} style={{width: 150}}>
                {dataCard?.createdBy}
              </TextCM>
            </View>
          )}
        </View>
        {renderStatusTicket()}
      </View>
      <TouchableOpacity hitSlop={15} onPress={() => setIsModalConfirm(true)}>
        <IconTrash width={20} height={20} />
      </TouchableOpacity>

      <ModalConfirmCM
        title={t('TK.delete_relate')}
        content={t('TK.delete_relate_confirm')}
        type="cancel"
        labelCancel={t('CM.labelCancel')}
        labelConfirm={t('CM.confirm')}
        isVisible={modalConfirm}
        onClose={() => setIsModalConfirm(false)}
        onConfirm={handleClearData}
      />
    </View>
  );
};

export default CardTicketRelative;
