import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../../../components/Header/HeaderCM';
import TextFieldCM from '../../../../components/TextField';
import {ScreenHeight, SizeDP} from '../../../../../constants/Size';
import styles from './styles';
import {IcSearch, IconFilter} from '../../../../../assets/images';
import BottomSheetCM from '../../../../components/BottomSheet';
import {useCallback, useEffect, useState} from 'react';
import {
  FlatList,
  Keyboard,
  Pressable,
  ScrollView,
  TouchableWithoutFeedback,
  View,
} from 'react-native';
import TextCM from '../../../../components/Text';
import DropdownListCM, {IDropDown} from '../../../../components/DropDownList';
import {dataPrioritySelect, dataStatus} from './helper';
import {
  ISearch,
  IServices,
  ITicket,
} from '../../../../../model/interface/service.interface';
import {useFocusEffect, useNavigation} from '@react-navigation/native';
import {debounce, isEmpty} from 'lodash';
import ButtonCM from '../../../../components/Button';
import GroupButtonCM from '../../../../components/GroupButton';
import {useAppSelector} from '../../../../../hooks/useRedux';
import CardCM from '../../../../components/Card';
import {CalendarRange, IndexPath, RangeDatepicker} from '@ui-kitten/components';
import {useTranslation} from 'react-i18next';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
const AddTicketRelative = ({route}: any) => {
  const {t} = useTranslation();
  const {
    onGoBack,
    paramsSelectTicket,
    codeChannel,
    checkSabeco = false,
  } = route?.params;
  const navigation = useNavigation();
  const [isVisible, setIsVisible] = useState<boolean>(false);
  const [groupService, setGroupService] = useState<IndexPath[]>([]);
  const [groupStatusTicket, setGroupStatusTicket] = useState<IndexPath[]>([]);
  const [groupServices, setGroupServices] = useState<IDropDown[]>([]);
  const [priority, setPriority] = useState<IndexPath[]>([]);
  const [totalRecoders, setTotalRecoders] = useState<number>(0);
  const [listTicket, setListTicket] = useState<ITicket[]>([]);
  const [listTicketSelect, setListTicketSelect] =
    useState<ITicket[]>(paramsSelectTicket);
  const [page, setPage] = useState<number>(0);
  const [reloading, setReloading] = useState<boolean>(false);
  const [textSearch, setTextSearch] = useState<string>('');
  const [debounceSearch, setDebounceSearch] = useState<string>('');
  const [range, setRange] = useState<CalendarRange<Date>>({});
  const {dataAccount} = useAppSelector(state => state.account);
  const token = useAppSelector(state => state.common.accessToken);
  const TicketService = useTicketServices();
  const debouncedSearch = debounce((text: string) => {
    setDebounceSearch(text);
  }, 500);

  useEffect(() => {
    debouncedSearch(textSearch);
    return () => {
      debouncedSearch.cancel();
    };
  }, [textSearch]);

  useEffect(() => {
    (async () => {
      await getListTicketRelative(page, debounceSearch);
    })();
  }, [page, debounceSearch]);

  useFocusEffect(
    useCallback(() => {
      (async () => {
        setPage(0);
        await getGroupService();
      })();
    }, []),
  );

  const handleSubmit = async () => {
    setPage(0);
    await getListTicketRelative(0, debounceSearch);
  };

  const getListTicketRelative = async (
    page: number,
    debounceSearch: string,
  ) => {
    try {
      const bodySubmit: ISearch = {
        createdBy: dataAccount?.email ?? 'elmstest7@gmail.com', //
        myTicket: true,
        textSearch: checkSabeco ? codeChannel : debounceSearch,
        ticketStatus:
          (groupStatusTicket.map(item => dataStatus[item.row].value) as any) ??
          [],
        service:
          groupService?.map(item => groupServices?.[item?.row]?.value) ?? [],
        priority:
          (priority?.map(
            item => dataPrioritySelect?.[item?.row]?.value,
          ) as any) ?? [],
        startDate: range?.startDate as any,
        endDate: range?.endDate as any,
        searchForSabeco: checkSabeco ? true : false,
      };

      const data = await TicketService.getTicketList(
        page,
        20,
        'createdDate,desc',
        token,
        bodySubmit,
      );
      if (page === 0) {
        setListTicket(data?.content);
      } else {
        setListTicket([...listTicket, ...data?.content]);
      }
      setTotalRecoders(data?.page?.totalRecords);
    } catch (error) {
    } finally {
      setReloading(false);
      setIsVisible(false);
    }
  };

  const getGroupService = async () => {
    try {
      const data = await TicketService.getServiceMenu(
        0,
        10000,
        undefined,
        token,
        true,
      );
      const filterData = data.content.map((item: IServices) => {
        return {
          label: item.serviceName,
          value: item.id,
        };
      });
      setGroupServices(filterData);
    } catch (error) {}
  };

  const renderListFilter = () => {
    return (
      <>
        {!isEmpty(groupServices) && (
          <SafeAreaView style={{maxHeight: (ScreenHeight * 2) / 3}}>
            <ScrollView showsVerticalScrollIndicator={false}>
              <TextCM style={{marginVertical: 4}}>
                {t('HM.groupService')}
              </TextCM>
              <DropdownListCM
                onSelect={setGroupService}
                valueSelect={groupService}
                placeholder={t('HM.groupServiceSelect')}
                multiple={true}
                listData={groupServices}
              />
              <TextCM style={{marginVertical: 4}}>{t('HM.priority')}</TextCM>
              <DropdownListCM
                onSelect={setPriority}
                valueSelect={priority}
                placeholder={t('HM.priotitySelect')}
                multiple={true}
                isi18n={true}
                listData={dataPrioritySelect}
              />

              <TextCM style={{marginVertical: 4}}>
                {t('HM.statusTicket')}
              </TextCM>
              <DropdownListCM
                onSelect={setGroupStatusTicket}
                valueSelect={groupStatusTicket}
                placeholder={t('HM.select')}
                multiple={true}
                isi18n={true}
                listData={dataStatus}
              />

              <TextCM style={{paddingVertical: SizeDP(10)}}>
                {t('HM.createDate')}
              </TextCM>
              <RangeDatepicker
                range={range}
                size="large"
                controlStyle={styles.calender}
                onSelect={nextRange => setRange(nextRange)}
                min={new Date(1900, 0, 0)}
                placeholder={props => (
                  <TextCM {...props}>{t('HM.selectDate')}</TextCM>
                )}
              />
              <View style={styles.bottom}></View>
            </ScrollView>

            <View style={{}}>
              <ButtonCM
                label={t('CM.save')}
                style={{marginTop: SizeDP(20)}}
                onPress={handleSubmit}
              />
            </View>
          </SafeAreaView>
        )}
      </>
    );
  };

  const fetchNextPage = () => {
    if (listTicket.length < totalRecoders) {
      setPage(page + 1);
    }
  };

  const onRefresh = () => {
    if (page !== 0) {
      setReloading(true);
    }
    setPage(0);
  };

  const handleBackCreateTicket = () => {
    onGoBack(listTicketSelect, codeChannel);
    navigation.goBack();
  };

  return (
    <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
      <>
        <HeaderCM title={t('HM.addRelativeTicket')} />
        <SafeAreaView style={styles.ctnArea} edges={['bottom']}>
          <View
            style={{
              backgroundColor: '#F8FAFE',
              paddingHorizontal: 16,
              paddingVertical: 10,
              flex: 1,
            }}>
            <TextFieldCM
              isDisabled={checkSabeco}
              inputContainerStyle={{backgroundColor: '#fff'}}
              viewIconLeft={<IcSearch width={16} height={16} />}
              viewIconRight={<IconFilter width={16} height={16} />}
              value={checkSabeco ? codeChannel : textSearch}
              onIconPress={() => setIsVisible(true)}
              onChangeText={setTextSearch}
              label={t('HM.searchTicket')}
            />
            <FlatList
              data={listTicket}
              refreshing={reloading}
              onRefresh={onRefresh}
              showsHorizontalScrollIndicator={false}
              showsVerticalScrollIndicator={false}
              contentContainerStyle={{paddingRight: SizeDP(2)}}
              onEndReached={fetchNextPage}
              onEndReachedThreshold={0.5}
              renderItem={({item}) => (
                <CardCM
                  dataCard={item}
                  onSelect={setListTicketSelect}
                  select={listTicketSelect}
                  account={dataAccount}
                />
              )}
            />
          </View>
          <View
            style={{
              backgroundColor: '#fff',
              paddingHorizontal: 16,
            }}>
            <GroupButtonCM
              containerStyle={{backgroundColor: '#fff', paddingTop: 6}}
              labelLeft={t('CM.labelCancel')}
              labelRight={`${t('CM.save')} (${listTicketSelect.length}) ${t(
                'HM.ticket',
              )}`}
              onPressLeft={() => navigation.goBack()}
              onPressRight={handleBackCreateTicket}
            />
          </View>
        </SafeAreaView>

        <BottomSheetCM
          isVisible={isVisible}
          title={t('CM.filter')}
          onClose={() => setIsVisible(false)}
          renderContent={renderListFilter()}
        />
      </>
    </TouchableWithoutFeedback>
  );
};

export default AddTicketRelative;
