import {FlatList, useWindowDimensions, View} from 'react-native';
import HeaderCM from '../../components/Header/HeaderCM';
import {useEffect, useState} from 'react';
import KanBan from './components/KanBan';
import {dataListStatusTicketFollow} from './helper';
import {useNavigation, useNavigationState} from '@react-navigation/native';
import {useAppDispatch} from '../../../hooks/useRedux';
import {ticketActions} from '../../../redux/slices';
import styles from './styles';
import {Icon, TopNavigationAction} from '@ui-kitten/components';
import {NavigationName} from '../../../constants';
import {useTranslation} from 'react-i18next';
import React from 'react';
import {TYPE_SCREEN} from '../../../constants/Size';
const MyTicketFollow = () => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const dispatch = useAppDispatch();
  const state = useNavigationState(state => state);
  const [statusSelect, setStatusSelect] = useState<number>(0);
  const {width} = useWindowDimensions();

  useEffect(() => {
    dispatch(ticketActions.setSelectTabBottom(state.index));
  }, [state.index]);

  const renderContentRight = () => {
    return (
      <>
        <TopNavigationAction
          icon={<Icon fill={'#ffffff'} name="search" />}
          onPress={() => {
            navigation.navigate(NavigationName.SearchTicketScreen, {
              type: 'ticket-follow',
            });
          }}
        />
      </>
    );
  };

  return (
    <View style={{flex: 1, position: 'relative'}}>
      <HeaderCM
        title={t('TK.ticketFollow')}
        renderContentRight={renderContentRight}
      />
      <FlatList
        style={{backgroundColor: '#fff'}}
        data={dataListStatusTicketFollow}
        horizontal={true}
        pagingEnabled={true}
        snapToAlignment="start"
        showsHorizontalScrollIndicator={false}
        showsVerticalScrollIndicator={false}
        snapToInterval={width}
        onScrollEndDrag={e => {
          const offsetX = e.nativeEvent.targetContentOffset?.x;
          if (offsetX) {
            const index = Math.round(offsetX / width);
            if (index < dataListStatusTicketFollow.length) {
              setStatusSelect(index);
            }
          } else {
            setStatusSelect(0);
          }
        }}
        decelerationRate="fast"
        contentContainerStyle={[styles.flatListContainer]}
        renderItem={({item}) => <KanBan dataListStatus={item} />}
      />

      <View
        style={{
          position: 'absolute',
          bottom: 5,
          alignItems: 'center',
          justifyContent: 'center',
          width: width,
        }}>
        <View style={styles.paging}>
          {Array.from(
            {
              length:
                width > TYPE_SCREEN.mobile
                  ? Math.ceil(dataListStatusTicketFollow.length / 2)
                  : dataListStatusTicketFollow.length,
            },
            (_, i) => ({
              id: `item-${i + 1}`,
            }),
          ).map((item, index) => (
            <View
              key={index}
              style={{
                width: 7,
                height: 7,
                borderRadius: 4,
                backgroundColor: index === statusSelect ? '#62248F' : '#E6EAF0',
              }}></View>
          ))}
        </View>
      </View>
    </View>
  );
};

export default MyTicketFollow;
