import {useFocusEffect} from '@react-navigation/native';
import {
  IServices,
  IServicesDetail,
} from '../../../../../model/interface/service.interface';
import {useCallback, useState} from 'react';
import {FlatList, View} from 'react-native';
import styles from './styles';
import {SafeAreaView} from 'react-native-safe-area-context';
import {IconEmptyOther} from '../../../../../assets/images';
import RenderListItem from '../SubServicesDetail';
import HeaderCM from '../../../../components/Header/HeaderCM';
import TextCM from '../../../../components/Text';
import {SizeDP} from '../../../../../constants/Size';
import {useAppSelector} from '../../../../../hooks/useRedux';
import {useTranslation} from 'react-i18next';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
const ServiceDetail = ({route}: any) => {
  const {t} = useTranslation();
  const dataDetail = route?.params as IServices;
  const [listData, setListData] = useState<any[]>([]);
  const {accessToken} = useAppSelector(state => state.common);
  const ListServices = useTicketServices();
  useFocusEffect(
    useCallback(() => {
      (async () => {
        const data = await ListServices.getByServiceId(
          dataDetail.id,
          accessToken,
        );
        setListData(handleGroupData(data));
      })();
    }, []),
  );

  const handleGroupData = (data: IServicesDetail[]) => {
    const map = new Map();
    data.forEach(data => {
      if (data.groupModelerId) {
        if (!map.has(data.groupModelerId)) {
          map.set(data.groupModelerId, [data]);
        } else {
          map.get(data.groupModelerId).push(data);
        }
      } else {
        if (!map.has(data.id)) {
          map.set(data.id, [data]);
        } else {
          map.get(data.id).push(data);
        }
      }
    });

    return Array.from(map.values());
  };

  return (
    <>
      <HeaderCM title={dataDetail.serviceName} />
      <SafeAreaView edges={['bottom', 'left', 'right']} style={styles.ctnMain}>
        <FlatList
          data={listData}
          renderItem={({item}) => <RenderListItem item={item} />}
          ListEmptyComponent={
            <View style={styles.ctnEmpty}>
              <IconEmptyOther width={SizeDP(95)} height={SizeDP(111)} />
              <TextCM>{t('HM.subServicesEmpty')}</TextCM>
            </View>
          }
        />
      </SafeAreaView>
    </>
  );
};

export default ServiceDetail;
