import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../components/Header/HeaderCM';
import TextCM from '../../components/Text';
import {useCallback, useState} from 'react';
import {INotification, INotificationItem} from './interface';
import {
  Divider,
  Icon,
  List,
  ListItem,
  Menu,
  MenuItem,
  Spinner,
  TopNavigationAction,
} from '@ui-kitten/components';
import {View} from 'react-native';
import {useTranslation} from 'react-i18next';
import {Font, NavigationName} from '../../../constants';
import styles from './styles';
import {useFocusEffect, useNavigation} from '@react-navigation/native';
import {Badge} from 'react-native-paper';
import {listAction} from './helper';
import {ITicket} from '../../../model/interface/service.interface';
import {useAppDispatch, useAppSelector} from '../../../hooks/useRedux';
import BottomSheetCM from '../../components/BottomSheet';
import {commonActions} from '../../../redux/slices';
import {useNotificationServices} from '../../../hooks/apiHooks/useNotificationServices';
import {useTicketServices} from '../../../hooks/apiHooks/useTicketServices';
import Toast from 'react-native-toast-message';
import React from 'react';

const NotificationScreen = () => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const dispatch = useAppDispatch();
  const [listNotifications, setListNotifications] = useState<INotification[]>(
    [],
  );

  const [page, setPage] = useState(0);
  const [totalRecoders, setTotalRecoders] = useState(0);
  const [refresh, setRefresh] = useState(false);
  const [isSelect, setIsSelect] = useState<{[key: string]: any}>({});

  const [menuVisible, setMenuVisible] = useState(false);
  const [selectStatus, setSelectStatus] = useState<'all' | 'read' | 'unread'>(
    'all',
  );

  const NotificationService = useNotificationServices();
  const TicketService = useTicketServices();

  const {accessToken, totalNumberNotiUnread} = useAppSelector(
    state => state.common,
  );
  const {dataAccount} = useAppSelector(state => state.account);

  const renderMenuAction = (): React.ReactElement => (
    <TopNavigationAction
      icon={<Icon fill={'#fff'} name="funnel-outline" />}
      onPress={() => setMenuVisible(true)}
    />
  );

  useFocusEffect(
    useCallback(() => {
      setPage(0);
      fetchDataNotification(0, selectStatus);
    }, [selectStatus]),
  );

  const fetchDataNotification = async (page: number, selectStatus: any) => {
    try {
      const params = {
        page: page,
        size: 30,
        sort: 'createdDate,desc',
      };
      const body = {
        status: selectStatus === 'all' ? '' : selectStatus,
        system: 'E_REQUEST',
      };
      const data = await NotificationService.getListNotifications(params, body);
      if (data) {
        if (page === 0) {
          setListNotifications(data.data);
        } else {
          setListNotifications([...listNotifications, ...data.data]);
        }
        setTotalRecoders(data.totalRecords);
      }
    } catch (error) {
    } finally {
      setRefresh(false);
    }
  };

  const getNotificationContent = (item: INotificationItem) => {
    if (item) {
      const params = {
        ticketName: item.ticketName,
        usertaskName: item.userTaskName,
        remindTime: item.remindTime,
        overdueTime: item.overdueTime,
        notifyTime: item.notifyTime,
        createdBy: item.createdBy,
        createdDate: item.createdDate,
        assigner: item.assigner,
        status: getTicketStatus(item.status),
        reason: item.reason ? item.reason : '',
        perm: item?.perm,
        accountCreated: item?.createdAcl,
      };
      if (item.type === 'acl_ticket_create' && item.userTaskName) {
        return t('NT.notification.' + item.type + '_user_task', params);
      } else {
        return t('NT.notification.' + item.type, params);
      }
    }
  };

  const getTicketStatus = (status: string) => {
    if (status && status.trim() === 'rejected') {
      return 't chi';
    } else if (status && status.trim() === 'completed') {
      return 'hon thnh';
    } else if (status && status.trim() === 'voided') {
      return 'hy';
    } else {
      return '';
    }
  };

  const onRefresh = () => {
    setRefresh(true);
    setPage(0);
    fetchDataNotification(0, selectStatus);
  };

  const getMoreData = () => {
    if (listNotifications.length < totalRecoders) {
      fetchDataNotification(page + 1, selectStatus);
      setPage(page + 1);
    }
  };

  const handleSelectNotification = async (item: INotification) => {
    try {
      if (item.type !== 'ticket_delete') {
        setIsSelect({[item.id]: true});
      }
      const body = [{id: item.id, status: 'read'}];
      const data = await NotificationService.updateNotifications(body);
      if (data) {
        const newListNoti = listNotifications.map(noti =>
          noti.id === item.id ? {...item, status: 'read'} : noti,
        );
        setListNotifications(newListNoti);
        dispatch(
          commonActions.setTotalNumberNotiUnread(totalNumberNotiUnread - 1),
        );
      }
      handleDirectNoti(item);
    } catch (error) {}
  };

  const handleDirectNoti = async (item: INotification) => {
    if (
      item.type === 'ticket_create' ||
      item.type === 'ticket_update' ||
      item.type === 'ticket_cancel' ||
      item.type === 'ticket_require_confirm' ||
      item.type === 'ticket_confirm' ||
      item.type === 'ticket_reject' ||
      item.type === 'ticket_require_review' ||
      item.type === 'ticket_review' ||
      item.type === 'ticket_review_reject' ||
      item.type === 'ticket_assign_user' ||
      item.type === 'ticket_update_status' ||
      item.type === 'ticket_authorize' ||
      item.type === 'ticket_remind_workflow' ||
      item.type === 'ticket_remind_usertask' ||
      item.type === 'ticket_remind_workflow_overdue_onetime' ||
      item.type === 'ticket_remind_usertask_overdue_onetime' ||
      item.type === 'ticket_remind_workflow_overdue_period' ||
      item.type === 'ticket_remind_usertask_overdue_period' ||
      item.type === 'ticket_tag_comment' ||
      item.type === 'acl_ticket_create'
    ) {
      const content = JSON.parse(item.content) as INotificationItem;
      if (content) {
        const data = (await TicketService.getTicketById(
          content.ticketId,
          accessToken,
        )) as ITicket;
        if (data) {
          navigation.navigate(NavigationName.BottomTabNavigationTicketDetail, {
            dataTicketId: content.ticketId,
            email: item.emailReceiver,
            status: data.ticketStatus,
            selectTabBottom:
              item.type === 'ticket_tag_comment' ||
              item.type === 'acl_ticket_create'
                ? 1
                : 2, // 1: tab from my ticket, 2: tab from ticket following
            recipientDetailsId: content.recipientDetailsId,
            userTaskId: content.userTaskId,
            notifyActionType: item.type,
          });
        } else {
          Toast.show({
            type: 'error',
            text1: t('CM.noti'),
            text2: t('CM.error'),
          });
        }
      }
      setIsSelect({[item.id]: false});
      return;
    }

    // if (item.type === 'ticket_delete') {
    //   viewList('ticket', 'delete');
    //   return;
    // }

    // if (
    //   item.type === 'service_group_create' ||
    //   item.type === 'service_group_update'
    // ) {
    //   viewList('service-group', 'update');
    //   return;
    // }

    // if (item.type === 'service_group_delete') {
    //   viewList('service-group', 'delete');
    //   return;
    // }
  };

  const renderItem = ({item, index}: {item: INotification; index: number}) => {
    const content = JSON.parse(item.content) as INotificationItem;
    const iconName =
      item.createdBy !== 'system' ? 'people-outline' : 'settings-outline';
    return (
      <>
        <ListItem onPress={() => handleSelectNotification(item)}>
          <View key={index} style={styles.ctnMain}>
            <View style={styles.ctnIcon}>
              <Icon width={20} height={20} fill={'#39ac6c'} name={iconName} />
              {item.status === 'unread' && (
                <Badge size={10} style={styles.badge}></Badge>
              )}
            </View>
            <View style={{flex: 1, rowGap: 10}}>
              <TextCM>{getNotificationContent(content)}</TextCM>
              <View style={styles.ctnText}>
                <TextCM
                  style={[
                    {
                      fontFamily: Font.InterSemiBold600,
                    },
                    styles.text,
                  ]}>
                  {''}
                </TextCM>
                <TextCM
                  style={[
                    [
                      {
                        fontFamily: Font.InterRegular400,
                      },
                      styles.text,
                    ],
                  ]}>
                  {content.notifyTime}
                </TextCM>
              </View>
            </View>
            {!isSelect?.[item?.id] ? (
              <Icon width={20} height={20} name="chevron-right-outline" />
            ) : (
              <Spinner size="tiny" />
            )}
          </View>
        </ListItem>
        <Divider />
      </>
    );
  };

  const onPressSelect = (status: any) => {
    setSelectStatus(status);
    setMenuVisible(false);
  };

  const renderAction = () => {
    return (
      <Menu>
        {listAction.map(action => (
          <MenuItem
            key={action.status}
            title={props => (
              <TextCM {...props}>
                {t(`NT.notification.filter.${action.status}`)}
              </TextCM>
            )}
            onPress={() => onPressSelect(action.status)}
            accessoryRight={
              selectStatus === action.status ? (
                <Icon name="checkmark" />
              ) : undefined
            }
          />
        ))}
      </Menu>
    );
  };

  return (
    <>
      <SafeAreaView edges={[]} style={{backgroundColor: '#943cdd0d', flex: 1}}>
        <HeaderCM title={t('CM.noti')} renderContentRight={renderMenuAction} />
        <List
          style={{backgroundColor: '#fff'}}
          contentContainerStyle={{backgroundColor: '#fff'}}
          data={listNotifications}
          onEndReachedThreshold={0.5}
          refreshing={refresh}
          onEndReached={getMoreData}
          onRefresh={onRefresh}
          renderItem={renderItem}
          ListEmptyComponent={() => (
            <View
              style={{
                alignItems: 'center',
                justifyContent: 'center',
                flex: 1,
                marginTop: 16,
              }}>
              <TextCM>{t('CM.emptyData')}</TextCM>
            </View>
          )}
        />
      </SafeAreaView>

      <BottomSheetCM
        title={t('CM.noti')}
        onClose={() => setMenuVisible(false)}
        isVisible={menuVisible}
        renderContent={renderAction()}
      />
    </>
  );
};

export default NotificationScreen;
