import {getConfig} from './httpClient';

export const createOriginServices = (fetchRequest: any) => ({
  async getOrigin(origin: string, accessToken: string) {
    const endPoint = `/services/uaa/api/organizations/${origin}`;
    try {
      const data = await fetchRequest({
        url: getConfig().BASE_URL + endPoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      return Promise.reject(error);
    }
  },
});
