import {TouchableOpacity, View} from 'react-native';
import TextCM from '../../../../components/Text';
import styles from './styles';
import {IconClock} from '../../../../../assets/images';
import {SizeDP} from '../../../../../constants/Size';
import React, {FC, useState} from 'react';
import {ITicket} from '../../../../../model/interface/service.interface';
import {DateUtils} from '../../../../../utils';
import {isEmpty} from 'lodash';
import {useNavigation} from '@react-navigation/native';
import {Color, Font, NavigationName} from '../../../../../constants';
import {useTranslation} from 'react-i18next';
import DropdownCM, {IDataDropdown} from '../../../../components/DropDown';
import {getListAction} from './helper';
import {Status} from '../../../../../model/enum/common.enum';
import {SegmentedButtons, Tooltip} from 'react-native-paper';
import {IListStatusTicket} from '../../../my-ticket/helper';
import {useAppSelector} from '../../../../../hooks/useRedux';
import {Divider, Icon} from '@ui-kitten/components';

type Props = {
  item: ITicket;
  dataListStatus: IListStatusTicket;
  handleCancelTicket: (item: ITicket) => void;
};

const KanBanItem: FC<Props> = React.memo(
  ({item, dataListStatus, handleCancelTicket}) => {
    const navigation = useNavigation<any>();
    const {t} = useTranslation();
    const [value, setValue] = useState('');
    const {dataAccount} = useAppSelector(state => state.account);

    const handleDirectTicketDetail = (typeView: 'view' | 'quick') => {
      navigation.navigate(NavigationName.BottomTabNavigationTicketDetail, {
        dataTicketId: item?.resourceId?.substr(4),
        email: item.sendFrom,
        status: dataListStatus.status,
        selectTabBottom: 2,
        recipientDetailsId: item.implementId,
        userTaskId: item.recipientId,
        typeView: typeView,
      });
    };

    const getTitle = () => {
      return isEmpty(item.resourceName) ? '---' : item.resourceName;
    };

    const getTitleTooltip = () => {
      if (item.status === Status.overdue) {
        if (checkRemindTime(item.remindTime, item.estimateDate)) {
          return (
            `${t('TK.before')} ` +
            getTimeRemind(item?.remindTime, item?.estimateDate)
          );
        }
        if (checkLate(item.estimateDate, null)) {
          return `${t('TK.late')} ` + getTimeLate(item?.estimateDate, null);
        }
      }
      if (
        (item.status === Status.completed || item.status === Status.accepted) &&
        checkLate(item.estimateDate, item.finishDate)
      ) {
        return (
          `${t('TK.late')} ` + getTimeLate(item?.estimateDate, item?.finishDate)
        );
      }
      return '';
    };

    const checkRemindTime = (remindTime: string, estimateTime: string) => {
      if (!remindTime) {
        return false;
      } else if (Number(remindTime) > 0) {
        const endTime = Math.floor(new Date(estimateTime).getTime() / 1000);
        const startTime = endTime - Number(remindTime) * 60;
        const currentTime = Math.floor(new Date().getTime() / 1000);
        return startTime < currentTime && currentTime < endTime;
      }
    };

    const checkLate = (estimateTime: string, finishDate?: any) => {
      if (!estimateTime) {
        return false;
      } else if (finishDate) {
        const endTime = Math.floor(new Date(estimateTime).getTime() / 1000);
        const finishTime = Math.floor(new Date(finishDate).getTime() / 1000);
        return finishTime > endTime;
      } else {
        const endTime = Math.floor(new Date(estimateTime).getTime() / 1000);
        const currentTime = Math.floor(new Date().getTime() / 1000);
        return currentTime > endTime;
      }
    };

    const getTimeLate = (estimateTime: string, finishDate: any) => {
      if (finishDate) {
        const endTime = Math.floor(new Date(estimateTime).getTime());
        const finishTime = Math.floor(new Date(finishDate).getTime());
        const time = finishTime - endTime;
        return convertTime(time);
      } else {
        const endTime = Math.floor(new Date(estimateTime).getTime());
        const currentTime = Math.floor(new Date().getTime());
        const time = currentTime - endTime;
        return convertTime(time);
      }
    };

    const getTimeRemind = (remindTime: string, estimateTime: string) => {
      if (Number(remindTime) > 0) {
        const endTime = Math.floor(new Date(estimateTime).getTime());
        const currentTime = Math.floor(new Date().getTime());
        const time = endTime - currentTime;
        return convertTime(time);
      }
    };

    const convertTime = (totalMilliseconds: number) => {
      const totalSeconds = Math.floor(totalMilliseconds / 1000);

      const days = Math.floor(totalSeconds / 86400);
      const remainingSecondsAfterDays = totalSeconds % 86400;

      const hours = Math.floor(remainingSecondsAfterDays / 3600);
      const remainingSecondsAfterHours = remainingSecondsAfterDays % 3600;

      const minutes = Math.floor(remainingSecondsAfterHours / 60);

      // const seconds = remainingSecondsAfterHours % 60;
      return `${days}d${hours}h${minutes}m`;
    };

    const handleAction = (itemDropdown: IDataDropdown) => {
      switch (itemDropdown.value) {
        case 'review':
          handleDirectTicketDetail('quick');
          return;
        case 'cancel':
          handleCancelTicket(item);
          return;
      }
    };

    return (
      <View style={styles.ctnMain}>
        <TouchableOpacity
          style={{
            padding: 16,
          }}
          onPress={() => handleDirectTicketDetail('view')}>
          <View style={styles.ctnHeader}>
            <TextCM style={[styles.title, {flex: 1, marginRight: SizeDP(12)}]}>
              {getTitle()}
            </TextCM>
            <View style={styles.time}>
              <View style={styles.center}>
                {!isEmpty(getTitleTooltip()) &&
                  dataListStatus.status === Status.processing && (
                    <Tooltip title={getTitleTooltip()}>
                      <IconClock width={SizeDP(16)} height={SizeDP(16)} />
                    </Tooltip>
                  )}
                {dataListStatus.status === Status.processing && (
                  <DropdownCM
                    listDropdown={getListAction(
                      item.status as any,
                      item.sendFrom
                        ? item.sendFrom === dataAccount.email
                        : true,
                    )}
                    onChangeItem={item => handleAction(item)}
                    containerStyle={{width: SizeDP(12)}}
                  />
                )}
              </View>
            </View>
          </View>
          <TextCM
            style={{
              marginBottom: SizeDP(10),
              color: '#6f2ced',
            }}>
            {t('TK.step')}: {item.notionName}
          </TextCM>
          <TextCM>
            {t('TK.createDate')}:{' '}
            {DateUtils.convertDateToDDMMYYYY_H_M(item.createdDate)}
          </TextCM>
          {!isEmpty(getTitleTooltip()) &&
            dataListStatus.status === Status.processing && (
              <TextCM style={styles.styleClock}>{getTitleTooltip()}</TextCM>
            )}
          <View style={styles.ctnFooter}>
            <View style={[styles.rowCenter]}>
              {/* <View style={[styles.viewId]}>
              <TextCM
                numberOfLines={1}
                style={{width: SizeDP(50)}}>{`#${item.id}`}</TextCM>
            </View> */}
              <View
                style={[
                  styles.viewStatus,
                  {
                    backgroundColor: dataListStatus.bgColor,
                    borderColor: '#fff',
                  },
                ]}>
                <TextCM style={[{color: dataListStatus.color}]}>
                  {t(`TK.${dataListStatus.status}`)}
                </TextCM>
              </View>
            </View>
          </View>
        </TouchableOpacity>
        <Divider />

        <SegmentedButtons
          value={value}
          onValueChange={setValue}
          density="medium"
          style={{overflow: 'hidden'}}
          buttons={
            dataListStatus.status === Status.processing
              ? [
                  {
                    value: 'view',
                    label: t('TK.view_ticket'),
                    icon: () => (
                      <Icon
                        fill={Color.Text}
                        name="eye-outline"
                        width={16}
                        height={16}
                      />
                    ),
                    labelStyle: styles.labelStyle,
                    style: {
                      borderRadius: 0.5,
                      borderColor: Color.borderColor,
                      borderWidth: 0,
                      borderRightWidth: 0.5,
                    },
                    onPress: () => handleDirectTicketDetail('view'),
                  },
                  {
                    value: 'quick',
                    label: t('TK.review_quick_ticket'),
                    icon: () => (
                      <Icon
                        fill={Color.Text}
                        name="external-link-outline"
                        width={16}
                        height={16}
                      />
                    ),
                    labelStyle: styles.labelStyle,
                    style: {
                      borderRadius: 0.5,
                      borderColor: Color.borderColor,
                      borderWidth: 0,
                      borderLeftWidth: 0.5,
                    },
                    onPress: () => handleDirectTicketDetail('quick'),
                  },
                ]
              : [
                  {
                    value: 'view',
                    label: t('TK.view_ticket'),
                    icon: () => (
                      <Icon
                        fill={Color.Text}
                        name="eye-outline"
                        width={16}
                        height={16}
                      />
                    ),
                    labelStyle: styles.labelStyle,
                    style: {
                      borderRadius: 8,
                      borderColor: Color.borderColor,
                      borderWidth: 0,
                    },
                    onPress: () => handleDirectTicketDetail('view'),
                  },
                ]
          }
        />
      </View>
    );
  },
);

export default KanBanItem;
