import {FlatList, View} from 'react-native';
import TextCM from '../../../../../../components/Text';
import Filter, {IFilter} from '../filter';
import {useEffect, useState} from 'react';
import {useDashboardServices} from '../../../../../../../hooks/apiHooks/useAPI';
import {Icon} from '@ui-kitten/components';
import styles from './styles';
import {useTranslation} from 'react-i18next';

const TicketRate = () => {
  const {t} = useTranslation();
  const [dataFilter, setDataFilter] = useState<IFilter>();
  const [dataService, setDataService] = useState<
    {
      rate: number;
      totalTickets: number;
    }[]
  >();
  const DashboardService = useDashboardServices();

  useEffect(() => {
    if (dataFilter) {
      getCountByService(dataFilter);
    }
  }, [dataFilter]);
  const getCountByService = async (dataFilter?: IFilter) => {
    const dataResp = await DashboardService.countByTicketRate(dataFilter);
    if (dataResp) {
      setDataService(dataResp);
    }
  };
  return (
    <View style={{flex: 1}}>
      <View style={styles.ctnMain}>
        <TextCM style={styles.txtHeader}>{t('DB.rate_ticket')}</TextCM>
        <Filter setDataFilter={setDataFilter} />
      </View>

      <FlatList
        data={dataService}
        showsVerticalScrollIndicator={false}
        contentContainerStyle={{rowGap: 16}}
        renderItem={({item}) => (
          <View style={styles.ctnBody}>
            <View style={styles.txtTitle}>
              {[1, 2, 3, 4, 5].map((itemX, index) => (
                <Icon
                  key={index}
                  fill={index < item?.rate ? '#fd6b04' : '#72839c'}
                  name={index < item?.rate ? 'star' : 'star-outline'}
                  width={16}
                  height={16}
                />
              ))}
            </View>

            <TextCM style={styles.txtDate}>{item.totalTickets}</TextCM>
          </View>
        )}
        ListEmptyComponent={() => <TextCM>{t('DB.no-data')}</TextCM>}
      />
    </View>
  );
};

export default TicketRate;
