import {FlatList, Image, View} from 'react-native';
import TextCM from '../../../../../../components/Text';
import Filter, {IFilter} from '../filter';
import {String} from '../../../../../../../constants';
import {useEffect, useState} from 'react';
import {useDashboardServices} from '../../../../../../../hooks/apiHooks/useAPI';
import {ITicket} from '../../../../../../../model/interface/service.interface';
import {DateUtils} from '../../../../../../../utils';
import styles from './styles';
import {useTranslation} from 'react-i18next';

const ServicesRecent = () => {
  const {t} = useTranslation();
  const [dataFilter, setDataFilter] = useState<IFilter>();
  const [dataService, setDataService] = useState<ITicket[]>();
  const DashboardService = useDashboardServices();

  useEffect(() => {
    if (dataFilter) {
      getCountByService(dataFilter);
    }
  }, [dataFilter]);
  const getCountByService = async (dataFilter?: IFilter) => {
    const dataResp = await DashboardService.getRecentTicket(dataFilter);
    if (dataResp) {
      setDataService(dataResp);
    }
  };
  return (
    <View style={{flex: 1}}>
      <View style={styles.ctnMain}>
        <TextCM style={styles.txtHeader}>{t('DB.ticket-nearest')}</TextCM>
        <Filter setDataFilter={setDataFilter} />
      </View>

      <FlatList
        data={dataService}
        showsVerticalScrollIndicator={false}
        contentContainerStyle={{rowGap: 16}}
        renderItem={({item}) => (
          <View style={styles.ctnBody}>
            <Image
              source={{uri: String.DEFAULT_IMAGE}}
              style={{width: 30, height: 30, borderRadius: 15}}
            />
            <View>
              <TextCM style={styles.txtTitle}>{item.title}</TextCM>
              <TextCM style={styles.txtDate}>
                {DateUtils.convertDateToDDMMYYYY_H_M(item.createdDate)}
              </TextCM>
            </View>
          </View>
        )}
        ListEmptyComponent={() => <TextCM>{t('DB.no-data')}</TextCM>}
      />
    </View>
  );
};

export default ServicesRecent;
