import {IconEdit, IconMulti, IconTrash} from '../../../../../assets/images';
import {SizeDP} from '../../../../../constants/Size';
import {Status} from '../../../../../model/enum/common.enum';

export const getListAction = (status: Status) => {
  switch (status) {
    case Status.processing:
      return [
        {
          id: 1,
          key: 'TK.multi',
          value: 'multi',
          icon: <IconMulti width={SizeDP(18)} height={SizeDP(18)} />,
        },
        {
          id: 2,
          key: 'TK.cancel',
          value: 'cancel',
          icon: <IconTrash width={SizeDP(18)} height={SizeDP(18)} />,
        },
      ];
    case Status.completed:
    case Status.rejected:
    case Status.voided:
    case Status.overdue:
      return [
        {
          id: 1,
          key: 'TK.multi',
          value: 'multi',
          icon: <IconMulti width={SizeDP(18)} height={SizeDP(18)} />,
        },
        // {
        //   id: 2,
        //   key: 'TK.cancel',
        //   value: 'cancel',
        //   icon: <IconTrash width={SizeDP(18)} height={SizeDP(18)} />,
        // },
      ];
    case Status.draft:
      return [
        // {
        //   id: 1,
        //   key: 'TK.multi',
        //   value: 'multi',
        //   icon: <IconMulti width={SizeDP(18)} height={SizeDP(18)} />,
        // },
        {
          id: 2,
          key: 'TK.edit',
          value: 'edit',
          icon: <IconEdit width={SizeDP(18)} height={SizeDP(18)} />,
        },
        {
          id: 3,
          key: 'TK.deleteTicket',
          value: 'delete',
          icon: <IconTrash width={SizeDP(18)} height={SizeDP(18)} />,
        },
      ];
    default:
      return [];
  }
};
