import {StyleProp, TextStyle, View} from 'react-native';
import TextCM from '../../../../components/Text';
import {Button, Datepicker, Icon} from '@ui-kitten/components';
import {Font} from '../../../../../constants';
import {useTranslation} from 'react-i18next';

type Props = {
  value: any;
  onChangeDate: (value: string | null) => void;
  label?: string;
  labelStyle?: StyleProp<TextStyle>;
  isRequired?: boolean;
  captionText?: string;
  status?:
    | 'basic'
    | 'primary'
    | 'success'
    | 'info'
    | 'warning'
    | 'danger'
    | 'control';
};

const InputDate = ({
  value,
  onChangeDate,
  label,
  labelStyle,
  status = 'basic',
  isRequired = false,
  captionText,
}: Props) => {
  const {t} = useTranslation();
  return (
    <>
      <Datepicker
        status={status}
        label={
          label
            ? props => (
                <TextCM
                  {...props}
                  style={[
                    labelStyle,
                    {
                      fontSize: 12,
                      fontFamily: Font.InterMedium500,
                      color: '#00204DF2',
                      marginBottom: 4,
                      lineHeight: 22,
                    },
                  ]}>
                  {label}{' '}
                  {isRequired && <TextCM style={{color: '#E14337'}}>*</TextCM>}
                </TextCM>
              )
            : undefined
        }
        caption={
          captionText
            ? props => (
                <View
                  {...props}
                  style={{
                    flexDirection: 'row',
                    alignItems: 'center',
                    justifyContent: 'flex-start',
                    columnGap: 10,
                    marginTop: 4,
                  }}>
                  <Icon fill={'#E14337'} width={14} height={14} name="info" />
                  <TextCM
                    style={{
                      fontSize: 12,
                      fontFamily: Font.InterMedium500,
                      color: '#E14337',
                    }}>
                    {captionText}
                  </TextCM>
                </View>
              )
            : undefined
        }
        renderFooter={() => (
          <View
            style={{
              flexDirection: 'row',
              alignItems: 'center',
              justifyContent: 'space-between',
            }}>
            <Button
              onPress={() => onChangeDate(null)}
              style={{marginHorizontal: 10}}
              children={props => <TextCM {...props}>{t('CM.del')}</TextCM>}
              status="danger"
              size="small"
            />
            <Button
              onPress={() => onChangeDate(new Date().toISOString())}
              style={{marginHorizontal: 10}}
              children={props => <TextCM {...props}>{t('CM.today')}</TextCM>}
              status="primary"
              size="small"
            />
          </View>
        )}
        controlStyle={{backgroundColor: '#fff'}}
        accessoryRight={
          <Icon
            fill={'#00204D8C'}
            name="calendar-outline"
            width={16}
            height={16}
          />
        }
        min={new Date(1900, 0, 0)}
        date={value ? new Date(value) : null}
        onSelect={nextDate =>
          onChangeDate(nextDate ? nextDate.toISOString() : null)
        }
      />
    </>
  );
};

export default InputDate;
