import {FlatList, Image, View} from 'react-native';
import TextCM from '../../../../../../components/Text';
import Filter, {IFilter} from '../filter';
import {String} from '../../../../../../../constants';
import {useEffect, useState} from 'react';
import {useDashboardServices} from '../../../../../../../hooks/apiHooks/useAPI';
import {ICountService} from '../../helper';
import {ProgressBar} from '@ui-kitten/components';
import {IconDefault} from '../../../../../../../assets/images';
import styles from './styles';
import {useTranslation} from 'react-i18next';

const Services = () => {
  const {t} = useTranslation();
  const [dataFilter, setDataFilter] = useState<IFilter>();
  const [dataService, setDataService] = useState<ICountService[]>();
  const DashboardService = useDashboardServices();

  useEffect(() => {
    if (dataFilter) {
      getCountByService(dataFilter);
    }
  }, [dataFilter]);
  const getCountByService = async (dataFilter?: IFilter) => {
    const dataResp = await DashboardService.getCountByService(dataFilter);
    if (dataResp) {
      const total = dataResp
        .map((filter: any) => filter.totalTickets)
        .reduce((total: number, item: any) => total + item, 0);
      const newData = dataResp.map((item: ICountService) => ({
        ...item,
        percent: item.totalTickets / total,
      }));
      setDataService(newData);
    }
  };

  return (
    <View style={{flex: 1}}>
      <View style={styles.ctnHeader}>
        <TextCM style={styles.txtHeader}>{t('DB.ticket-service')}</TextCM>
        <Filter setDataFilter={setDataFilter} />
      </View>

      <FlatList
        data={dataService}
        showsVerticalScrollIndicator={false}
        contentContainerStyle={{rowGap: 16}}
        style={{flex: 1}}
        renderItem={({item}) => (
          <View style={styles.ctnBody}>
            <View style={styles.ctnSubBody}>
              <View>
                <View style={[styles.ctnImage]}>
                  {!item.iconBase64 ? (
                    <IconDefault width={20} height={20} />
                  ) : (
                    <Image
                      width={20}
                      height={20}
                      borderRadius={10}
                      source={{
                        uri: `${String.PREFIX_IMG_BASE64}${item.iconBase64}`,
                      }}
                    />
                  )}
                </View>
              </View>
              <View style={{flex: 1}}>
                <TextCM style={styles.txtService}>
                  {item?.serviceName?.trim()}
                </TextCM>
                <TextCM style={styles.txtDescription}>
                  {item.description}
                </TextCM>
              </View>
            </View>
            <View
              style={{
                flex: 1,
              }}>
              <TextCM style={styles.txtPercent}>{`${
                item?.percent ? (Number(item.percent) * 100).toFixed(2) : 0
              }%`}</TextCM>
              <ProgressBar animating={false} progress={item.percent} />
            </View>
          </View>
        )}
        ListEmptyComponent={() => <TextCM>{t('DB.no-data')}</TextCM>}
      />
    </View>
  );
};

export default Services;
