import {createAIServices} from '../../services/ai.services';
import {createDashboardServices} from '../../services/dashboard.service';
import {useFetchRequest} from '../useFetch';

export const useAIServices = () => {
  const fetchRequest = useFetchRequest();
  return createAIServices(fetchRequest);
};

export const useDashboardServices = () => {
  const fetchRequest = useFetchRequest();
  return createDashboardServices(fetchRequest);
};
