import {
  ActivityIndicator,
  FlatList,
  useWindowDimensions,
  View,
} from 'react-native';
import {SafeAreaView} from 'react-native-safe-area-context';
import TextCM from '../../../../components/Text';
import React, {FC, useCallback, useState} from 'react';
import styles from './styles';
import KanBanItem from '../KanBanItem';
import {ITicket} from '../../../../../model/interface/service.interface';
import {Color} from '../../../../../constants';
import {useAppSelector} from '../../../../../hooks/useRedux';
import Toast from 'react-native-toast-message';
import {IListStatusTicket} from '../../../my-ticket/helper';
import {useTranslation} from 'react-i18next';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import ModalConfirmCM from '../../../../components/ModalConfirm';
import TextFieldCM from '../../../../components/TextField';
import {useFocusEffect} from '@react-navigation/native';
import {Status} from '../../../../../model/enum/common.enum';
import LoadingMore from '../../../../components/LoadingMore';
import {Divider, List} from '@ui-kitten/components';
import Skeleton from '../../../../components/Skeleton';
import {TYPE_SCREEN} from '../../../../../constants/Size';
type Props = {
  dataListStatus: IListStatusTicket;
};

const KanBan: FC<Props> = ({dataListStatus}) => {
  const {t} = useTranslation();
  const [listDataTicket, setListDataTicket] = useState<ITicket[]>([]);
  const [totalRecoders, setTotalRecoders] = useState<number>(0);
  const [page, setPage] = useState<number>(0);
  const [reloading, setReloading] = useState<boolean>(false);
  const [bottomSheetVisible, setBottomSheetVisible] = useState(false);
  const [reason, setReason] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isLoadMore, setIsLoadMore] = useState(false);
  const [ticketSelect, setTicketSelect] = useState<ITicket>();
  const {accessToken} = useAppSelector(state => state.common);
  const ListServices = useTicketServices();
  const {width} = useWindowDimensions();

  const toogleBottomSheet = () => {
    setReason('');
    setBottomSheetVisible(!bottomSheetVisible);
  };

  const handleCancelTicketSelect = (item: ITicket) => {
    setTicketSelect(item);
    toogleBottomSheet();
  };

  useFocusEffect(
    useCallback(() => {
      getListTicket(0, false);
    }, []),
  );

  const getTerm = () => {
    const term = {} as {status?: string; resourceStatus?: string};
    if (dataListStatus.status === Status.processing) {
      term.status = dataListStatus.status;
      term.resourceStatus = dataListStatus.status;
    } else if (dataListStatus.status === Status.overdue) {
      term.status = Status.processing;
      term.resourceStatus = Status.overdue;
    } else if (dataListStatus.status === Status.voided) {
      term.resourceStatus = dataListStatus.status;
      term.status = Status.processing;
    } else {
      term.status = dataListStatus.status;
    }
    return term;
  };

  const getListTicket = async (page: number, isLoadMore: boolean) => {
    try {
      if (isLoadMore) {
        setIsLoadMore(true);
      } else {
        setIsLoading(true);
      }
      const search = {
        keyWord: '',
        terms: getTerm(),
        id: null,
      };

      const data = await ListServices.getTicketListFollow(
        page,
        20,
        dataListStatus.status === Status.processing
          ? 'createdDate,desc'
          : 'lastModifiedDate,desc',
        search,
        accessToken,
      );

      if (page === 0) {
        setListDataTicket(data?.content);
      } else {
        setListDataTicket([...listDataTicket, ...data?.content]);
      }
      setTotalRecoders(Number(data?.page?.totalRecords));
    } catch (error) {
    } finally {
      setReloading(false);
      setIsLoadMore(false);
      setIsLoading(false);
    }
  };

  const fetchNextPage = () => {
    if (listDataTicket.length < totalRecoders) {
      getListTicket(page + 1, true);
      setPage(page + 1);
    }
  };

  const onRefresh = () => {
    setReloading(true);
    setPage(0);
    getListTicket(0, false);
  };

  const handleCancelTicket = async (reason: string) => {
    try {
      if (reason?.length === 0) {
        setError('CM.requireFillReason');
        return;
      }
      const params = {
        ticketId: ticketSelect?.resourceId?.split('sid_')[1] ?? '',
        reason: reason,
      };
      const data = await ListServices.cancelTicket(params, accessToken);
      if (data.data) {
        Toast.show({
          text1: t('CM.noti'),
          type: 'success',
          text2: t('TK.rejectSuccess'),
        });
      } else {
        const message = data?.error?.message;
        Toast.show({
          text1: t('CM.noti'),
          type: 'error',
          text2: t(`TK.${message || 'rejectFailure'}`),
        });
      }
      toogleBottomSheet();
    } catch (error) {}
  };

  if (isLoading && !isLoadMore) {
    return (
      <List
        data={Array(8).fill({})}
        horizontal={true}
        pagingEnabled={true}
        snapToAlignment="start"
        showsHorizontalScrollIndicator={false}
        showsVerticalScrollIndicator={false}
        snapToInterval={width}
        decelerationRate="fast"
        contentContainerStyle={[
          {
            backgroundColor: '#fff',
            flex: 1,
          },
        ]}
        style={{
          width: width > TYPE_SCREEN.mobile ? width / 2 : width,
        }}
        renderItem={() => <Skeleton />}
        ItemSeparatorComponent={Divider}
      />
    );
  }

  return (
    <>
      <SafeAreaView
        style={[
          styles.ctnMain,
          {
            width: width > TYPE_SCREEN.mobile ? width / 2 : width,
          },
        ]}
        edges={['left', 'right']}>
        <View style={styles.ctnHeader}>
          <View
            style={[styles.ctnNumber, {backgroundColor: dataListStatus.color}]}>
            <TextCM style={styles.text}>{totalRecoders}</TextCM>
          </View>
          <View
            style={[styles.ctnText, {backgroundColor: dataListStatus.color}]}>
            <TextCM style={styles.text}>
              {t(`TK.name_${dataListStatus.status}`)}
            </TextCM>
          </View>
        </View>
        {listDataTicket && (
          <FlatList
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            data={listDataTicket}
            refreshing={reloading}
            onRefresh={onRefresh}
            onEndReached={fetchNextPage}
            onEndReachedThreshold={0.5}
            keyExtractor={item => '' + item.id}
            renderItem={({item}) => (
              <KanBanItem
                item={item}
                dataListStatus={dataListStatus}
                handleCancelTicket={handleCancelTicketSelect}
              />
            )}
            ListEmptyComponent={() => (
              <View
                style={{
                  alignItems: 'center',
                  justifyContent: 'center',
                  flex: 1,
                }}>
                <TextCM>{t('CM.emptyData')}</TextCM>
              </View>
            )}
            ListFooterComponent={() => isLoadMore && <LoadingMore />}
          />
        )}
      </SafeAreaView>

      {bottomSheetVisible && (
        <ModalConfirmCM
          isVisible={bottomSheetVisible}
          onClose={toogleBottomSheet}
          title={`${t('TK.confirmClear')} ${ticketSelect?.resourceName}`}
          type="cancel"
          labelCancel={t('HM.cancel')}
          labelConfirm={t('CM.labelCancel')}
          styleBtnRight={{backgroundColor: Color.RedE14337}}
          onConfirm={() => handleCancelTicket(reason)}
          renderContent={() => (
            <View style={{width: '100%', marginVertical: 6}}>
              <TextFieldCM
                value={reason}
                onChangeText={setReason}
                errorMessage={t(error)}
                isRequired={true}
                multiline
                isFloatingLabel={true}
                label={t('CM.fillReason')}
                maxLength={400}
              />
            </View>
          )}
        />
      )}
    </>
  );
};

export default KanBan;
