import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {useRoute} from '@react-navigation/native';
import WebView from 'react-native-webview';
import {String} from '../../../../../constants';
import {useTranslation} from 'react-i18next';

const ViewFileScreenEcontract = () => {
  const {t} = useTranslation();
  const route = useRoute<any>();
  const iframeUrl = route?.params?.iframeUrl ?? '';
  return (
    <SafeAreaView edges={['bottom']} style={{flex: 1, backgroundColor: '#fff'}}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        title={t('RV.contract')}
        contentStyle={{
          backgroundColor: '#fff',
        }}
      />
      {iframeUrl && (
        <WebView
          startInLoadingState
          injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
          allowsInlineMediaPlayback={true}
          mediaPlaybackRequiresUserAction={false}
          domStorageEnabled={true}
          javaScriptEnabled={true}
          thirdPartyCookiesEnabled={true}
          source={{uri: iframeUrl}}
        />
      )}
    </SafeAreaView>
  );
};

export default ViewFileScreenEcontract;
