import {
  IconPerson,
  IconRelative,
  IconTrash,
} from '../../../../../assets/images';
import {SizeDP} from '../../../../../constants/Size';
import {Status} from '../../../../../model/enum/common.enum';

export const getListAction = (status: Status, isCheck: any) => {
  switch (status) {
    case Status.processing:
      return isCheck
        ? [
            {
              id: 1,
              key: 'TK.review',
              value: 'review',
              icon: <IconPerson width={SizeDP(18)} height={SizeDP(18)} />,
            },
            {
              id: 2,
              key: 'TK.cancel',
              value: 'cancel',
              icon: <IconTrash width={SizeDP(18)} height={SizeDP(18)} />,
            },
          ]
        : [
            {
              id: 1,
              key: 'TK.review',
              value: 'review',
              icon: <IconPerson width={SizeDP(18)} height={SizeDP(18)} />,
            },
          ];
    default:
      return [];
  }
};
